.container {
    height: 100vh;
    padding: 20px;
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-template-rows: 1fr;
    grid-column-gap: 20px;

}

.data {
    width: 100%;
}

@media (max-width: 992px) {
    .container {
        display: block;

    }
}




